body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Add a black background color to the top navigation */
.topnav {
  background-color: #333;
  overflow: hidden;
}

/* Style the links inside the navigation bar */
.topnav a {
  float: left;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

/* Change the color of links on hover */
.topnav a:hover {
  background-color: #ddd;
  color: black;
}

/* Add a color to the active/current link */
.topnav a.active {
  background-color: #04AA6D;
  color: white;
}

.webname {
  color: white;
  float: right;
  font-weight: bold;
  font-size: large;
  text-align: center;
  font-size: 25px;
  padding: 12px 12px;
}
.info {
    padding-left: 1%;
    padding-right: 10%;
    padding-top: 1%;
    padding-bottom: 1%;
}

.info p{
    font-size: larger;
}
/* Style for the container of the image list */
.image-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  /* Style for each individual image item */
  .image-item {
    width: calc(33.33% - 10px);
    margin-bottom: 100px;
    position: relative;
  }
  
  /* Style for the image within each item */
  .image-item img {
    width: 100%;
    height: auto;
    border-radius: 4px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  /* Style for the overlay text */
  .image-info {
    position: relative;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    color: #fff;
    padding: 10px;
    box-sizing: border-box;
  }
  
  /* Style for the title of the image */
  .image-info h2 {
    margin-top: 0;
    margin-bottom: 5px;
    font-size: 1.2rem;
    font-weight: bold;
  }
  
  /* Style for the description of the image */
  .image-info p {
    margin: 0;
    font-size: 0.9rem;
  }
